<template>
  <div class="home">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item
        v-for="(item, index) in banner.list"
        :key="index"
        @click="goShow(item.extra_data.app_banner_param)"
      >
        <img :src="item.image" alt />
      </van-swipe-item>
    </van-swipe>
    <HomeSection v-if="today" :items="today.list" :special="true">今日</HomeSection>
    <HomeSection v-if="hot" :items="hot.list">热门</HomeSection>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapActions } from "vuex";
import HomeSection from "../components/HomeSection";
import types from "@/store/TYPES.js";
// console.log(types)
export default {
  name: "Home",
  components: {
    HomeSection
  },
  computed: {
    ...mapState({
      banner: state => state.indexData.banner,
      album: state => state.indexData.album,
      hot: state => state.indexData.hot,
      posts: state => state.indexData.posts,
      today: state => state.indexData.today
    })
  },
  methods: {
    ...mapActions([types.INIT_INDEX_DATA]),
    goShow: function(a) {
      console.log(a);
      this.$router.push({ name: "video", params: { videoId: a } });
    }
  },
  created() {
    this.$store.dispatch(types.INIT_INDEX_DATA, { n: 10 });
  }
};
</script>

