<template>
  <div class="card" @click="goShow(item.postid)">
    <img :src="item.image" alt="" />
    <dl>
      <dt>
        <span v-for="(cate, index) in item.cates" :key="index">{{
          cate.catename
        }}</span
        >/<span>{{ item.duration }}</span>
      </dt>
      <dd>{{ item.title }}</dd>
    </dl>
  </div>
</template>
<script>
export default {
  props: ["item"],
  methods: {
    goShow: function(a) {
      console.log(a);
      this.$router.push({ name: "video", params: { videoId: a } });
    }
  },
};
</script>
<style lang="less" scoped>
.card {
  position: relative;
  img {
    vertical-align: middle;
    max-width: 100%;
    filter: brightness(0.8);
  }
  dl {
    position: absolute;
    left: 0;
    top: 0;
    padding: 15px;
    color: white;
    dt {
      span {
      }
    }
  }
}
</style>
