<script>
export default {
  data() {
    return {
      active: "home",
      dayDetil: [],
      height: {
        height: ""
      },
      show: false
    };
  },
  methods: {
    navClick: function(name, title) {
      console.log(name, title);
      switch (name) {
        case "home":
          this.$router.push("/");
          break;
        case "channel":
          this.$router.push("/channel");
          break;
        case "Main":
          this.$router.push("/Main");
          break;
      }
    },
    showToday: function() {
      this.show = true;
      // this.height.top = '50px'
    },
    getheigth: function() {
      this.height.height = window.innerHeight + "px";
    },
    gettodayDetil: function() {
      this.axios
        .get(`/apiv3/DayCover/getDayCover`)
        .then(res => {
          this.dayDetil = res.data.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    hideToday: function() {
      this.show = false;
    }
  },
  created() {
    this.getheigth();
    this.gettodayDetil();
  }
};
</script>
